.movie__item {
  position: relative;
}

.movie__item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 0;
  border-left: 4px solid #dc2626;
  transition: all 0.3s linear;
}

.movie__item:hover::after {
  height: 100%;
}

.movie__item img {
  transform: scale(100%);
  transition: all 0.3s linear;
}

.movie__item:hover img {
  transform: scale(105%);
}

.btn__primary {
  position: relative;
}

.btn__primary::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background-color: rgba(0, 0, 0, 0.3);
  transition: all 0.2s linear;
}

.btn__primary::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 0;
  background-color: rgba(0, 0, 0, 0.3);
  transition: all 0.2s linear;
}

.btn__primary:hover::after,
.btn__primary:hover::before {
  width: 50%;
}

.footer {
  position: relative;
  right: 0;
  left: 0;
  max-width: 2000px;
  margin: 0 auto;
}

.header__catalog {
  position: relative;
}

.footer::before,
.footer::after,
.header__catalog::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.header__catalog::after {
  background-image: linear-gradient(to top, #262626, rgba(0, 0, 0, 0.7));
}

.bg__play {
  position: relative;
}

.bg__play::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 20, 20, 0.8);
  z-index: 0;
}

/* FOOTER */

.footer-bg {
  background-image: linear-gradient(to bottom, #262626, rgba(0, 0, 0, 0.8));
}

.footer-bg {
  background-color: rgba(0, 0, 0, 0.2);
}

.footer__social a {
  background-color: #262626;
}

.footer__social a:hover {
  box-shadow: 0 0 0.625em #dc2626;
  color: #dc2626;
  transition: all 0.3s ease;
}

.footer__social svg {
  fill: #d1d5db;
  transition: all 0.3s ease;
}

.footer__social a:hover svg {
  fill: #dc2626;
}

.banner__title,
.banner__rate,
.banner__desc,
.banner__btn {
  opacity: 0;
  transform: translateX(-100px);
  transition: all 1s ease;
}

.banner__image {
  opacity: 0;
  transform: translateX(500px);
  transition: all 1.7s ease;
}

.slick-slide.slick-active .banner__title,
.slick-slide.slick-active .banner__rate,
.slick-slide.slick-active .banner__desc,
.slick-slide.slick-active .banner__btn,
.slick-slide.slick-active .banner__image {
  opacity: 1;
  transform: translateX(0);
}

.slick-slide.slick-active .banner__rate {
  transition-delay: 0.3s, 0.3s;
}

.slick-slide.slick-active .banner__desc {
  transition-delay: 0.5s, 0.5s;
}

.slick-slide.slick-active .banner__btn {
  transition-delay: 0.7s, 0.7s;
}

